import { costemStorage } from "@/utils";
import axios from "axios";

export const costemAxios = axios.create({
    baseURL: '/api',
    headers: { 'Content-Type': 'application/json', }
});

costemAxios.interceptors.request.use(
    (config: any) => {
        config.headers['X-User-Password'] = costemStorage.local.get('auth');
        return config;
    }
);