import dayjs from "dayjs";
import duration from 'dayjs/plugin/duration';
import utc from 'dayjs/plugin/utc';
import timezone from 'dayjs/plugin/timezone';
import { NextRequest, NextResponse } from "next/server";
import { CustomError, CustomResponse, getCategories, isPassword, passwordError, scheduler, supabase } from "@api/props";
import { lostArkApi } from "../route";
import { deepCopy, } from "@/utils";

dayjs.extend(duration);
dayjs.extend(utc);
dayjs.extend(timezone);

interface LostArkEventEtcProp {
    end: string,
    reward: string,
    title: string,
    is_clear: boolean,
}

export const initLostArkEventEtcProp: LostArkEventEtcProp = {
    end: ``,
    reward: ``,
    title: ``,
    is_clear: false,
}

export interface LostArkEventProp {
    event: {
        title: string,
        img: string,
        link: string,
        date: {
            start: string,
            end: string,
            reward: string,
        }
    },
    profiles: {
        account: string,
        character: number,
        is_clear: boolean,
    }[];
}

export const initLostArkEventProp: LostArkEventProp = {
    event: {
        title: ``,
        img: ``,
        link: ``,
        date: {
            start: ``,
            end: ``,
            reward: ``,
        }
    },
    profiles: []
}

const setEventArray = (array: []): any => array.reduce((accumulator: any[], current: any) => {
    const result = {
        id: current.id,
        profile: current.profile,
        is_clear: current.etc.is_clear,
    }
    const duplicate = accumulator.find((group) => group.title === current.etc.title)
    if (duplicate) { duplicate.data.push(result); }
    else {
        accumulator.push({
            title: current.etc.title,
            end: current.etc.reward || current.etc.end,
            update_at: current.update_at,
            data: [result]
        });
    }
    return accumulator;
}, [])

export const eventPatten = async (events: any): Promise<any> => {
    let contents: LostArkEventProp[] = [];
    try {
        //! Ìï¥ÎãπÏöîÏùºÏù¥ ÏïÑÎãêÎïå ÎÇ¥Í∞Ä Ï†ïÎ†¨ÏùÑ ÏïàÌïúÎã§..
        const setScheduler = await scheduler({ type: 'LOA', name: 'EVENT' }, async (contents, isToday) => {
            const contentsArray = setEventArray(contents);
            if (true) { // isTodayÎ°ú Î∞îÍæ∏Í∏∞
                const today = dayjs().tz('Asia/Seoul');
                for (const arry of contentsArray) {
                    if (today.isAfter(dayjs(arry.end).tz('Asia/Seoul'), `day`)) {
                        const { error: deletError }: any = await supabase.rpc(`delete_loa_event_contents`, {
                            p_id: arry.data.map((data: any) => data.id),
                        })
                        if (deletError) { throw undefined; } else { break; }
                    }
                    const { error: updateError }: any = await supabase.rpc(`update_loa_event_contents`, {
                        p_id: arry.data.map((data: any) => data.id),
                        p_is_clear: false,
                    });
                    console.log(updateError);
                    if (updateError) { return undefined; }
                    //! Ïó¨Í∏∞ ÌôïÏù∏ÌïòÍ∏∞ ÌÅ¥Î¶¨Ïñ¥Ïó¨Î∂Ä Ï¥àÍ∏∞Ìôî 
                    console.log(arry);
                }
            }
            return contentsArray;
        });
        for (const event of events) {
            const profiles = setScheduler.find((group: any) => group.title === event.Title);
            contents.push({
                ...deepCopy(initLostArkEventProp),
                event: {
                    title: event.Title,
                    img: event.Thumbnail,
                    link: event.Link,
                    date: {
                        start: event.StartDate,
                        end: event.EndDate,
                        reward: event.RewardDate,
                    },
                },
                profiles: profiles !== undefined ? profiles.data : [],
            })
        }
    }
    catch (error: any) { throw error; }
    return contents;
}

export async function GET(req: NextRequest) {
    if (!isPassword(req.headers.get("X-User-Password"))) { return passwordError; }
    try {
        const events = await lostArkApi.event();
        const response = await eventPatten(events);
        return CustomResponse(response);
    }
    catch (e: any) {
        if (e instanceof CustomError) {
            console.error(`${e.message}`, e.error);
            return NextResponse.json(
                { message: e.message, data: [], },
                { status: e.status },
            );
        }
        return NextResponse.json(
            { message: 'An unexpected error occurred during API processing.', data: [], },
            { status: 500 },
        );
    }
}

export async function POST(req: NextRequest) {
    if (!isPassword(req.headers.get("X-User-Password"))) { return passwordError; }
    try {
        const { end, reward, title } = await req.json();
        const category = await getCategories(`LOA`, `EVENT`);
        const { error: initError }: any = await supabase.rpc(`insert_loa_event_contents`, {
            p_category: category,
            p_etc: {
                ...deepCopy(initLostArkEventEtcProp),
                end: end,
                reward: reward,
                title: title,
            }
        });
        if (initError) { throw new CustomError('loa/event => insert error', initError, 500) }
        return CustomResponse();
    }
    catch (e: any) {
        console.log('loa/event => insert error:', e.error);
        return NextResponse.json(
            { message: e.message, data: [], },
            { status: e.status },
        );
    }
}

export async function DELETE(req: NextRequest) {
    if (!isPassword(req.headers.get("X-User-Password"))) { return passwordError; }
    try {
        const { id } = await req.json();
        const { error: deleteError }: any = await supabase.rpc(`delete_loa_event_contents`, {
            p_id: id,
        })
        if (deleteError) { throw new CustomError('loa/event => delete error', deleteError, 500) }
        return CustomResponse();
    }
    catch (e: any) {
        console.log('loa/event => delete error:', e.error);
        return NextResponse.json(
            { message: e.message, data: [], },
            { status: e.status },
        );
    }
}